//fetch.js
import { fetch ,fetchAdmin} from './fetch'
//getData(),url,type,datafetch(config)config
export  function getData(url,type,data) {
//typepost
    if(type===''){
      type = 'post';
      return fetch({
        url: url,
        method: type,
        data: data
      })
    }
    if(type=='post'){
      return fetch({
        url: url,
        method: type,
        data: data
      })
    }
    if(type=='patch'){
      return fetch({
        url: url,
        method: type,
        data: data
      })
    }
    if(type=='delete'){
      return fetch({
        url: url,
        method: type,
        data: data
      })
    }
    if(type=='get'){
      return fetch({
        url: url,
        method: type,
        params: data
      })
    }
}


export  function getAdminData(url,type,data) {
//typepost
    if(type===''){
      type = 'post';
      return fetchAdmin({
        url: url,
        method: type,
        data: data
      })
    }
    if(type=='post'){
      return fetchAdmin({
        url: url,
        method: type,
        data: data
      })
    }
    if(type=='patch'){
      return fetchAdmin({
        url: url,
        method: type,
        data: data
      })
    }
    if(type=='get'){
      return fetchAdmin({
        url: url,
        method: type,
        params: data
      })
    }
}



