<template>
  <div>
    <Header title="" />
    <van-address-list
      v-model="chosenAddressId"
      :list="list"         
      @add="onAdd"
      @edit="onEdit"
      add-button-text=""
    />
    
    
  </div>
</template>
<script>
import Header from '@/components/Header'
export default {
  name: 'PayMents', 
  components: {
    Header
  },
  data () {
    return { 
      chosenAddressId: '1',
      list: [
        {
          id: '1',
          name: '',
          tel: '13000000000',
          address: ' 138  7  501 '
        },
        {
          id: '2',
          name: '',
          tel: '1310000000',
          address: ' 50 '
        }
      ]      
    }
  },
  mounted () {

  },
  methods: {
    onAdd() {
      this.$router.push({ name: 'NewAddress'})
      // Toast('');
    },

    onEdit(item, index) {
      this.$router.push({ name: 'NewAddress'})
      // Toast(':' + index);
    }
  }
}
</script>
<style lang="scss" scoped> 
  .van-address-list__add{
    width: 100%;
  }
  
</style>



