import axios from 'axios'
//fetchconfig
export function fetch(config){
   //promise
   return new Promise((resolve,reject) =>{
       //axios
       const instance = axios.create({
           //
           headers:{
              'Content-Type': 'application/json',
              'Authorization':localStorage.token?localStorage.token:'',
              'Platform':2
            },
            timeout: 10000,
            //
            // baseURL: 'http://localhost:8080'
        });
       //
        instance(config).then(res =>{
            // console.log(res)
            resolve(res);
        //
        }).catch(err => {
            reject(err);
        })
    });
}


//
export function fetchAdmin(config){
   //promise
   return new Promise((resolve,reject) =>{
       //axios
       const instance = axios.create({
           //
           headers:{
              'Content-Type': 'application/json',
              'Authorization':localStorage.workerToken?localStorage.workerToken:'',
              'Platform':2
            },
            timeout: 10000,
            //
            // baseURL: 'http://localhost:8080'
        });
       //
        instance(config).then(res =>{
            resolve(res);
        //
        }).catch(err => {
            reject(err);
        })
    });
}



