<template>
  <div>
    <Header title="/" />
    <van-address-edit
      :area-list="areaList"
      show-postal
      show-delete
      show-set-default
      show-search-result
      :search-result="searchResult"
      @save="onSave"
      @delete="onDelete"
      @change-detail="onChangeDetail"
    />
    
  </div>
</template>
<script>
import Header from '@/components/Header'
import Vue from  'vue'
import { AddressEdit } from 'vant'
Vue.use(AddressEdit)

const areaList = {
  province_list: {
    110000: '',
    120000: ''
  },
  city_list: {
    110100: '',
    110200: '',
    120100: '',
    120200: ''
  },
  county_list: {
    110101: '',
    110102: '',
    110105: '',
    110106: '',
    120101: '',
    120102: '',
    120103: '',
    120104: '',
    120105: '',   
  }
}
export default {
  name: 'NewAddress', 
  components: {
    Header
  },
  data () {
    return { 
      areaList,
      searchResult: [] 
    }
  },
  mounted () {

  },
  methods: {
    onSave() {
      // Toast('save');
    },
    onDelete() {
      // Toast('delete');
    },
    onChangeDetail(val) {
      if (val) {
        this.searchResult = [{
          name: '',
          address: ''
        }];
      } else {
        this.searchResult = [];
      }
    }
  }
}
</script>
<style lang="scss" scoped> 
  
</style>



