<template>
  <div>
    <Header title="" />
    <div class="atteatation">
      <div class="main">
        <div class="topIco"></div>
        <div class="atteatInput">
          <van-cell-group>
            <van-field
              v-model="phone"
              required
              clearable
              label=""
              right-icon="question-o"
              placeholder=""
            />
           <van-field
              v-model="sms"
              center
              required
              clearable
              label=""
              placeholder=""
            >
            
              <van-button slot="button" size="small" type="primary" @click="getMsg" v-model="verifyCodeTips">{{verifyCodeTips}}</van-button>
              
            </van-field>
            <van-field
              v-model="name"
              required
              clearable
              label=""
              right-icon="question-o"
              placeholder=""
            />
            <van-field
              v-model="idCard"
              required
              clearable
              label=""
              right-icon="question-o"
              placeholder=""
            />
          </van-cell-group>
          <div class="uploadBox">
            <h3><i>*</i></h3>
            <ul class="uploadUl flex">
              <li class="flex1 border-right">
                <div class="bigImg flex_row">
                   <!--  <img src="~@/assets/uhdgf.png" alt="" > -->
                   <img :src="idcard.front" alt="" >  
                   <!--     -->
                </div>
              </li>
              <li class="flex1">
                <van-uploader :after-read="onRead"></van-uploader> 
                <!--     -->
              </li>
            </ul> 
            <p>##</p>
          </div>
          <div class="uploadBox">
            <h3><i>*</i></h3>
            <ul class="uploadUl flex">
              <li class="flex1 border-right">
                <div class="bigImg flex_row">
                   <!--<img src="~@/assets/okjd.png" alt="">-->
                  <img :src="idcard.back" alt="" >  
                  <!--     -->
                </div>
              </li>
              <li class="flex1">
                <van-uploader :after-read="onRead1"></van-uploader>  
                <!--   -->
              </li>
            </ul> 
            <p>##</p>
          </div>
          <div class="btmBtn" @click="submit">
            <van-button :disabled="false" type="danger"><span></span></van-button>  
            <!--     -->
          </div>
        </div>
      </div>
    </div>    
  </div>
</template>
<script>
import Header from '@/components/Header'
import axios from 'axios'


export default {
  name: 'Mine', 
  components: {
    Header
  },
  data () {
    return { 
    		info:'',
    	 	phone:'',
    	 	sms:'',
    	 	idCard:'',
    	 	name:'',   
    	 	idcard_pic1:'', // "~@/assets/uhdgf.png"
    	 	idcard_pic2:'', // 
    	 	 countdown: 60,
    	 	 verifyCodeTips: '',
    	 	 codeEnable: true,
    	 	 thumb: '',
    	 	 type: '',
			idcard: {
				'front':require('@/assets/uhdgf.png'),
				'back': require('@/assets/okjd.png')// 
			}
    }
  },
  
  mounted () {
  	//console.log(this.$route.params.id)
  },

  methods:{
  	  getMsg(){
  	//alert("")
  	 if(!this.codeEnable){
      			return
      }
  	 if(!(/^1[3456789]\d{9}$/.test(this.phone))){ 
        alert("");  
        return; 
    	} 
    	 axios({
      	url:"http://192.168.1.192/duobao/api.php/public/sendmessage",
      	method:"post",
      	params:{
						mobile:this.phone
      	}
      }).then(response => {
      	  (this.info = response.data.member)
      	  console.log(response.data.member)
      	  this.settime();
      	}
     )
      .catch(function (error) { // 
        console.log(error);
        console.log(mobile)
      });
     
     },
  	settime:function () {
        var that = this
        if (that.countdown == 1) {
            that.countdown = 60;
            that.verifyCodeTips = "";//
            that.codeEnable = true;
            return;
        } else {
            that.countdown--;
            that.verifyCodeTips = "(" + that.countdown + ")";
            that.codeEnable = false
        }
        setTimeout(function(){
            that.settime();
            
        }, 1000)
    },
  	
    onRead(file) {
	  //console.log(str) // str  
     // console.log(file) // file ajax
     // console.log(event)
	  let fromdata = new FormData() // fromdata
	  let that = this // this 
	  fromdata.append('image', file.file) // file
      axios({
      	url:"http://192.168.1.192/duobao/api.php/public/uploadimg",  //   
      	method:"post",
//    	params:{
////						idcard_pic1:null,/**/
////						idcard_pic2:null/**/
//			// image: file
//			fromdata
//    	}
				headers:{"Content-type":"multipart/form-data"},
				data:fromdata
      }).then(response => {
      	 // (this.infosc = response.data)
		 // that.idcard[str] = response.map.image  //  dataidcard  
		 that.idcard.front = response.data.image
		// that.idcard.back = response.data.image
		 console.log(response.data)
		// console.log(JSON.parse(response.data))
		// console.log(response.data.image)
		// 	console.log(response)
      	}
     )
      .catch(function (error) { // 
        console.log(error);
      });
    },
     onRead1(file) {
	  //console.log(str) // str  
     // console.log(file) // file ajax
     // console.log(event)
	  let fromdata = new FormData() // fromdata
	  let that = this // this 
	  fromdata.append('image', file.file) // file
      axios({
      	url:"http://192.168.1.192/duobao/api.php/public/uploadimg",  //   
      	method:"post",
//    	params:{
////						idcard_pic1:null,/**/
////						idcard_pic2:null/**/
//			// image: file
//			fromdata
//    	}
				headers:{"Content-type":"multipart/form-data"},
				data:fromdata
      }).then(response => {
      	 // (this.infosc = response.data)
		 // that.idcard[str] = response.map.image  //  dataidcard  
		// that.idcard.front = response.data.image
		 that.idcard.back = response.data.image
		 console.log(response.data)
		// console.log(JSON.parse(response.data))
		 console.log(response.data.image)
		 	console.log(response)
      	}
     )
      .catch(function (error) { // 
        console.log(error);
      });
    },
    submit () {
	   const that = this // this 
       axios({
      	url:"http://192.168.1.192/duobao/api.php/member/certification",
      	method:"post",
      	params:{
      			id:this.$route.params.id,
						mobile: this.phone, /*data.username:*/
						//mobile:data.sms, /*data.sms:(this.sms   )  */
						realname: this.name, /*data.name:*/
						verify:this.sms,/**/
						idcard: this.idCard, /**/
						idcard_pic1: that.idcard.front, /**/
						idcard_pic2: that.idcard.back /**/
      	}
      }).then(response => {
      	  (this.info = response.data.member)
      	}
     )
      .catch(function (error) { // 
        console.log(error);
      });
  },

    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .atteatation{
    padding: 0.7rem 0.3rem 0.3rem;
    .van-button--primary{
      background: none;
      border: 1px solid #e64535;
      border-radius: 0.5rem;      
      span.van-button__text{
        color: #e64535!important;       
      }
    }
    .main{
      background: #fff;
      width: 100%;
      min-height: 3.0rem;
      border-radius: 0.15rem;
      box-shadow: 0 0 0.15rem #ddd;
      position: relative;
      .topIco{
        position: absolute;
        left: 0;
        right: 0;
        margin: auto;
        text-align: center;
        top: -0.42rem;
        width: 0.72rem;
        height: 0.72rem;
        border-radius: 0.72rem;
        border: 0.04rem solid #fff;
        background: url('~@/assets/cvbgh.png') no-repeat center;
        background-size: 100% auto;
      }
      .atteatInput{
        padding: .44rem .4rem;
        .van-field .van-cell__title{
          max-width: 0.7rem
        }
        .uploadBox{
          width: 100%;
          h3{
            padding-left: 0.1rem;
            font-size: 0.3rem;
            line-height: 0.8rem;
            padding-top: 0.2rem;
            i{
              color: #fd4040;
            }
          }
          p{
            color: #fd4040;
            font-size: 0.24rem;
            line-height: 0.44rem;
          }
          .uploadUl{
            width:100%;
            height: 2.75rem;
            background: #f5f5f5;
            border: 1px solid #dedede;
            border-radius: 0.1rem;
            li{
              background: url('~@/assets/jnhdgf.png') no-repeat center;
              background-size: 1.12rem auto;
              .van-uploader{
                display: block;
                width: 100%;
                height: 100%;
              }
              .bigImg{
                width: 100%;
                height: 100%;
                background: #fff;
                img{
                  max-width: 1.7rem;
                  height: 1.1rem;                  
                }
              }
            }
          }
        }
        .btmBtn{
          width: 100%;
          height: 0.88rem;
          margin-top: 0.5rem;         
          button{
            width: 100%;
            height: 100%;
            color: #fff;
            span{
              color: #fff;
              font-size: 0.3rem; 
            }
          }
        }
      }
    }
  }
</style>



